import { Component, OnInit, Input, Output, EventEmitter, SimpleChanges } from '@angular/core';

import { AccountsService } from 'src/app/api/controllers/Accounts';
import { SharedService } from 'src/app/shared/services/shared.service';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SessionService } from 'src/app/core/session/session.service';

import * as _ from 'underscore';

@Component({
  selector: 'app-income-post-collection',
  templateUrl: './income-post-collection.component.html',
  styleUrls: ['./income-post-collection.component.scss']
})
export class IncomePostCollectionComponent implements OnInit {
  
  collection:any = {};

  instrumentTypeListData:any;
  paymentStatusListData:any;
  glAccountListData:any;
  depositSlipLisData:any;

  isCollectionSubmitted:boolean = false;

  isError:boolean = false;
  alertMessage:string = "";

  minCollectionAmount:number = 1;
  maxCollectionAmount:any = 0;

  isMultipleEntry:boolean = false;

  @Input() totalAmountArray:any;
  @Input() invoiceIdArray:any;

  custInvoiceObjArray:any;

  @Output() collectionParams = new EventEmitter(false);

  constructor(
    private accountsService:AccountsService,
    private lookupService:LookupService,
  	private sharedService:SharedService,
  	private sessionService:SessionService
  ) { }

  getInstrumentType(event) {
    this.collection.instrumentTypeId = event[0].lookupValueId
  }

  getDepositSlip(event){
    this.collection.depositSlipNumber = event[0].id
  }

  getPaymentStatus(event){
    this.collection.collectionStatusId = event[0].lookupValueId
  }

  submitIncomePostCollectionForm() {

    var custInvoiceObjArray = [];

    if(this.invoiceIdArray.length == 1){

      _.each(this.invoiceIdArray, (item, index) => {
        
        var details = {
            
            "apartmentId": this.sessionService.apartmentId,
            "collectionId": 0,
            "invoiceId": item,
            "glAccountId": 0,
            "instrumentTypeId": parseInt(this.collection.instrumentTypeId),
            "amount": this.collection.amount,
            "comment": "",
            "isActive": true,
            "insertedBy": parseInt(this.sessionService.userId),
            "insertedOn": "2020-01-10T06:59:54.422Z",
            "updatedBy": 0,
            "updatedOn": "2020-01-10T06:59:54.422Z"
        }

        custInvoiceObjArray.push(details);

      })

    }
    else {

      _.each(this.invoiceIdArray, (item, index) => {
        
        var details = {
            
            "apartmentId": this.sessionService.apartmentId,
            "collectionId": 0,
            "invoiceId": item,
            "glAccountId": 0,
            "instrumentTypeId": parseInt(this.collection.instrumentTypeId),
            "amount": this.totalAmountArray[index],
            "comment": "",
            "isActive": true,
            "insertedBy": parseInt(this.sessionService.userId),
            "insertedOn": "2020-01-10T06:59:54.422Z",
            "updatedBy": 0,
            "updatedOn": "2020-01-10T06:59:54.422Z"
        }

        custInvoiceObjArray.push(details);

      })

    }

    

    	this.isCollectionSubmitted = false;

    	let collectionDetails = {

    		"apartmentId": this.sessionService.apartmentId,
    		"receiptNumber": 0,
    		"receiptDate": new Date().toISOString(),
    		"amount": parseInt(this.collection.amount),
    		"instrumentTypeId": parseInt(this.collection.instrumentTypeId),
    		"postOn": this.collection.postOn || new Date().toISOString(),
    		"collectionAccountTypeId": 165, // account number
    		"depositSlipNumber": 0,
    		"postExcessAmountTo": 0,
    		"collectionStatusId": parseInt(this.collection.collectionStatusId),
        "collectionStatus": "",
    		"isEmailSent": true,
    		"isSmssent": true,
    		"comment": this.collection.comment || "",
    		"isActive": true,
    		"insertedBy": parseInt(this.sessionService.userId),
    		"insertedOn": new Date().toISOString(),
    	  "updatedBy": null,
        "updatedOn": null,
        "invoiceId": this.invoiceIdArray[0],
        "transReference1": "",
        "transReference2": "",
        "glAcctIndicator": "Asset",
    		"collectionInvoice" : custInvoiceObjArray
       }
       
       let params = {
        collection: collectionDetails
       }

    this.accountsService.addCollection(params).subscribe((res:any) => {

      if(res.message){

           this.isCollectionSubmitted = true;
           this.sharedService.setAlertMessage("Collection done successfully");
           this.collectionParams.emit(true);
         }
         else {

            this.isCollectionSubmitted = true;
            this.isError = true;
            this.alertMessage = res.errorMessage;
         }

    },
    error => {
      this.isCollectionSubmitted = true;
    });


  }

  ngOnInit() {

  	this.collection = {};
  	this.collection.instrumentTypeId = "";
  	this.collection.collectionAccountTypeId = "";
  	this.collection.depositSlipNumber = "";
  	this.collection.collectionStatusId = "";

    let paymentListParams = {
      LookupTypeId: 34
    }

  	//payment status
    this.lookupService.getLookupValueByLookupTypeId(paymentListParams).subscribe((res:any) => {
          
       this.paymentStatusListData = res.filter( item => {
          return item.isActive;
        });

    },
    error => {
      
    }
    );

    let insListParams = {
      LookupTypeId: 33
    }

  	//payment mode
    this.lookupService.getLookupValueByLookupTypeId(insListParams).subscribe((res:any) => {
          
       this.instrumentTypeListData = res.filter( item => {
          return item.isActive;
        });

    },
    error => {
      
    }
    );

    var accountParams = {
      apartmentId: this.sessionService.apartmentId,
      groupId: 3
    }

    this.accountsService.getGlAccountsByGroupId(accountParams).subscribe((res:any) => {

  		this.glAccountListData = res;

  	});

    this.depositSlipLisData = [{id:1},{id:2},{id:3},{id:4}];
    this.isCollectionSubmitted = true;

    this.collection.amount = parseFloat(this.totalAmountArray[0]).toFixed(2)
    this.maxCollectionAmount = this.collection.amount

  }


  ngOnChanges(changes: SimpleChanges) {

    if(this.totalAmountArray.length > 1){
      
      this.isMultipleEntry = true;
      this.collection.amount = this.totalAmountArray.reduce((a, b) => {
        return a + b
      }).toFixed(2)

      this.maxCollectionAmount = this.collection.amount;

    }
    else {
      this.isMultipleEntry = false;
      this.collection.amount = parseFloat(this.totalAmountArray[0]).toFixed(2)
      this.maxCollectionAmount = this.collection.amount
    }

  }

}
